<template>
  <div>
    <el-form :inline="true" :model="params" class="demo-form-inline">
      <el-form-item label="财务组织:">
        {{company_name}}
      </el-form-item>
      <el-form-item label="业务日期">
        <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="客户名称:">
        {{customer_name}}
      </el-form-item>
      <el-form-item size="mini" class="search-style search">
        <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
        <el-button plain @click="onReset" size="mini">重置</el-button>
      </el-form-item>
    </el-form>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" :summary-method="getSummaries" show-summary style="width: 100%">
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="price" label="预收" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="occupy" label="预收占用" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.occupy  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="use_deposits" label="使用预收" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.use_deposits  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="balance" label="余额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.balance  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_sn" label="关联单据"></el-table-column>
      </el-table>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">返 回</el-button>
    </div>
  </div>
</template>

<script>
import { getData } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customeCustomerdepositsSearch",
  data() {
    return {
      paramsParams: {},
      params: {},
      timeValue: "",
      company_name: "",
      customer_name: "",
      TableData: {
        tableData: []
      },
      total_balance: 0
    };
  },
  computed: {
    ...mapGetters(["customerdepositsSearch_showGet"])
  },
  methods: {
    ...mapActions(["customerdepositsDefault_show", "customerdepositsIndexACT"]),

    onSearch() {
      this.company_name = this.customerdepositsSearch_showGet.company_name;
      this.supplier_name = this.customerdepositsSearch_showGet.supplier_name;

      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.$axios({
        method: "get",
        url: this.$api.getCustomDepositsInfo,
        params: {
          ...this.params,
          customer_id: this.customerdepositsSearch_showGet.customer_id,
          company_id: this.customerdepositsSearch_showGet.company_id
        }
      }).then(({ data }) => {
        this.TableData.tableData = data.data.list;
        this.total_balance = data.data.account.total_balance;
      });
    },
    onReset() {
      this.timeValue = [];
      this.$axios({
        method: "get",
        url: this.$api.getCustomDepositsInfo,
        params: {
          customer_id: this.customerdepositsSearch_showGet.customer_id,
          company_id: this.customerdepositsSearch_showGet.company_id
        }
      }).then(({ data }) => {
        this.TableData.tableData = data.data.list;
        this.total_balance = data.data.account.total_balance;
      });
    },
    goBack() {
      Object.assign(this.customerdepositsSearch_showGet.params, {
        company_name: this.customerdepositsSearch_showGet.company_name,
        customer_name: this.customerdepositsSearch_showGet.customer_name
      });
      this.customerdepositsIndexACT({
        // this.customerdepositsDefault_show({ // wz alter
        // commont: "customerdepositsDefault",
        params: this.customerdepositsSearch_showGet.params,
        goPage: this.customerdepositsSearch_showGet.goPage,
        pageSize: this.customerdepositsSearch_showGet.pageSize
      });
      this.$router.push({
        path: "/custom/customerdeposits"
      });
    },
    getSummaries(param) {
      // 合并计算
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "合计";
          return;
        }
        if (index === 4) {
          sums[index] = Number(this.total_balance)
            .toFixed(2)
            .toString()
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
        } else {
          sums[index] = "";
        }
      });
      return sums;
    }
  },
  computed: {
    ...mapGetters(["customerdepositsSearch_showGet"])
  },
  created() {
    if (this.customerdepositsSearch_showGet.customer_id != undefined) {
      this.company_name = this.customerdepositsSearch_showGet.company_name;
      this.customer_name = this.customerdepositsSearch_showGet.customer_name;
      this.$axios({
        method: "get",
        url: this.$api.getCustomDepositsInfo,
        params: {
          customer_id: this.customerdepositsSearch_showGet.customer_id,
          company_id: this.customerdepositsSearch_showGet.company_id
        }
      }).then(({ data }) => {
        this.TableData.tableData = data.data.list;
        if (data.data.account) {
          // wz alter 如果有account字段就进行赋值
          this.total_balance = data.data.account.total_balance;
        }
      });
    }
  }
};
</script>

